var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_vm._ssrNode("<article class=\"product\" data-v-3ec1c09a><div class=\"left\" data-v-3ec1c09a><h1 class=\"title\" data-v-3ec1c09a>Uunikala</h1><span data-v-3ec1c09a>6 annosta</span><span class=\"recipe-title\" data-v-3ec1c09a>Ainekset:</span><ul data-v-3ec1c09a><li data-v-3ec1c09a>1 kokonainen noin 1,5 kiloinen punalihainen kala, esim. lohi, kirjolohi, nieriä tai taimen</li><li data-v-3ec1c09a>1 rkl karkeaa merisuolaa</li><li data-v-3ec1c09a>6 isoa perunaa</li><li data-v-3ec1c09a>6 Varpion tomaattia</li><li data-v-3ec1c09a>6 oksaa rosmariinia</li><li data-v-3ec1c09a>1 sitruuna</li><li data-v-3ec1c09a>2 ruukkua Varpion pinaattia</li><li data-v-3ec1c09a>Loraus oliiviöljyä</li><li data-v-3ec1c09a>Hyppysellinen suolaa</li><li data-v-3ec1c09a>6 rouhaisua mustapippuria myllystä</li></ul><span class=\"recipe-title\" data-v-3ec1c09a>Valmistusohjeet</span><ol data-v-3ec1c09a><li data-v-3ec1c09a>Perkaa kala ja poista kidukset, pyyhi lima nahan päältä pois ja halutessasi vielä suomusta kala sekä laita merisuola kalan sisään. Tämän kaiken voit myös pyytää kalakauppiastasi tekemään. Tämä kalan esikäsittely on syytä tehdä hyvissä ajoin, ainakin muutamaan tuntia ennen aterian valmistusta.</li><li data-v-3ec1c09a>Kuori ja leikkaa perunat noin sentin paksuisiksi viipaleiksi. (* Leikkaa perunaviipaleisiin useita syviä viiltoja, samaan tyyliin kuin hasselbackanperunoihinkin.)</li><li data-v-3ec1c09a>Viipaloi tomaatit myös noin sentin paksuisiksi viipaleiksi.</li><li data-v-3ec1c09a>Asettele perunat ja tomaatit leivinpaperin päälle uunipellille lomittain, nypi päälle rosmariinin lehtiä. Jätä keskellä leivinpaperia pitkä tyhjä tila, johon myöhemmin asetat kalan. Pirskottele päälle hyvin maltillisesti oliiviöljyä (kalasta tulee paiston aikana irtoamaan runsaasti rasvaa), hienoa suolaa ja mustapippuria. Laita viimeistään tässä vaiheessa uuni kuumenemaan 225 asteeseen.</li><li data-v-3ec1c09a>Ota kala kylmästä ja tarvittaessa huuhtele ylimääräinen suola ja kalan lima pois kylmän juoksevan veden alla. Kuivaa kala huolellisesti.</li><li data-v-3ec1c09a>Aseta kala työlaudalle ja tee toiseen kylkeen syvät viillot noin kahden sentin välein. Laita kalan sisään 4 oksaa rosmariinia ja noin puolikkaan sitruunan kuoret, ei valkoista osaa. *Saat kuoriviipaleet parhaiten pestystä sitruunasta kuorimaveitsellä.</li><li data-v-3ec1c09a>Nosta kala sille varattuun tilaan leivinpaperilla ja laita koko komeus uuniin. Kalaa ja lisukkeita paistetaan uunissa noin puoli tuntia 225 asteessa.</li><li data-v-3ec1c09a>Kalan ollessa jo lähes kypsä, kuumenna paistinpannu pinaattia varten. Laita pinaatit kuumaan pannuun, jossa on hiukan oliiviöljyä. Mausta pienellä määrällä suolaa ja ota pinaatit pois pannusta. Pinaatin paistamisen kanssa saa olla nopea, jotta väri säilyisi vielä kauniin vihreänä.</li><li data-v-3ec1c09a>Tarkasta paiston jälkeen, että kala ja perunat ovat varmasti kypsiä. Tarvittaessa laske lämpö 150 asteeseen (näin kypsyminen jatkuu, mutta ruoka ei pala) ja jatka paistamista, kunnes kaikki on kypsää.</li><li data-v-3ec1c09a>Ota uunipelti pois uunista, poista kalan nahka (vain päällimmäiseltä puolelta) sekä mausteet kalan sisältä.</li><li data-v-3ec1c09a>Asettele paistetut pinaatit perunoiden ja tomaattien päälle ja purista hiukan sitruunan mehua kalan ja kasvisten päälle. Tarjoile kuumana.</li></ol></div><div class=\"right\" data-v-3ec1c09a><img"+(_vm._ssrAttr("src",require("assets/img/recipes/uunikala.jpg")))+" data-v-3ec1c09a></div></article>")])}
